export default {
	name: "",
	components: {

	},
	props: {},
	data() {
		return {
			values: [{
					value: 'male',
					name: '男'
				},
				{
					value: 'female',
					name: '女'
				}
			],
			actived: null,
			sex:''
		}
	},
	methods: {
		iconBack() {
			uni.navigateBack()
		},
		change(item, index) {
			console.log(item)
			console.log(index)
			this.actived = index
			this.sex=index
		},
		submit() {
			let user = this.userinfo
			console.log(this.userinfo.userUniqueToken)
			uni.request({
				url: `https://www.imovietrailer.com/superhero/user/modifyUserinfo?qq=2684425594`,
				method: 'POST',
				data: {
					birthday: user.birthday,
					nickname: user.names,
					sex: this.sex,
					userId: user.id
				},
				header: {
					'headerUserId': user.id,
					'headerUserToken': user.userUniqueToken,
					'Content-Type': 'application/json'
				},
				success: res => {
					console.log(res)
					if (res.data.status === 200) {
						this.$store.state.userInfo = res.data.data
						uni.navigateBack()
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	mounted() {
		this.userinfo = this.$store.state.userInfo
		this.actived=this.$store.state.userInfo.sex
	},
	onLoad() {

	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
